/*
 Component Name : Amexio  Dropdown Menu
 Component Selector : <amexio-drop-down-menu-items>
 Component Description : Amexio Dropdown component with various modes and configurations .
 */

import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
@Component({
  selector: 'amexio-drop-down-menu-item',
  template: `
    <div *ngIf="!label " class="dropdowndivider" style="padding-bottom: 5px;">
      <ng-content></ng-content>
    </div>
    <a *ngIf=" label  " (click)="onItemClick($event)" [ngClass]="{'dropdowndivider':separator}">

      <ng-container *ngIf="icon">
        <em [style.float]="iconalign" [ngClass]="icon"></em>
      </ng-container>

      <ng-container *ngIf="!icon">
        <em [style.float]="iconalign" class="fa fa-folder-o"></em>
      </ng-container>

      <em [style.float]="iconalign" [ngClass]="iconAdded"></em>
      <span [style.float]="labelalign" style="padding: 5px;">
        {{label}}
      </span>
    </a>
  `,
})
export class AmexioDropDownitemsComponent implements OnInit {
  toggle: boolean;
/*
   Properties
   name : label
   datatype : string
   version : 4.2 onwards
   default :
   description : Label Dropdown
   */
  @Input() label: string;
  /*
   Properties
   name : icon
   datatype : string
   version : 4.2 onwards
   default :
   description : icon on DropDown Menu
   */
  @Input() icon: string;
  /*
   Properties
   name : label-align
   datatype : string
   version : 4.2 onwards
   default :
   description : Label-align for label right/left
   */
  @Input ('label-align') labelalign: string;
  /*
   Properties
   name : icon-align
   datatype : string
   version : 4.2 onwards
   default :
   description : icon-align for text field right/left
   */
  @Input ('icon-align') iconalign: string;
  /*
   Properties
   name : add-seperator
   datatype :  boolean
   version : 4.2 onwards
   default : false
   description : add divider to the dropdown menu list
   */
  @Input ('separator') separator: boolean;

  /*
   Properties
   name : add-seperator
   datatype :  boolean
   version : 4.2 onwards
   default : false
   description : add divider to the dropdown menu list
   */

  @Output() onClick: any = new EventEmitter<any>();
  ngOnInit() {
    if (this.iconalign && this.labelalign
      && this.iconalign.toLowerCase() === this.labelalign.toLowerCase()) {
      this.iconalign = null;
      this.labelalign = null;
    } else if (this.iconalign && this.iconalign === 'left') {
      this.labelalign = 'right';
      this.iconalign = null;
    } else if (this.iconalign && this.iconalign === 'right') {
      this.labelalign = null;
    }
  }
  onItemClick(clickEvent: any) {
  this.toggle = false;
  const e = {
      event: clickEvent,
      this: this,
    };
  this.onClick.emit(e);
  }
}
