/*
 Component Name : Amexio sidenav node
 Component Selector : <amexio-sidenav-node>
 Component Description : The Side Nav Bar Component is a familiar side
 navigation pattern for users. Side nav bar can be placed on left or right side.
 It can fit as many navigation links as needed, scrolling when the content
 exceeds the viewport. Take a look at Datastructure format which this component can consume in datasource tab.
*/
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'amexio-sidenav-node', template: `
    <ul>
      <li  *ngFor="let node of data">

        <div [attr.draggable]="enabledrag" (dragstart)="dragStartEvent({event:$event,data:node})" class="defaultnode" [ngClass]="{'activenode':node.active}" (click)="getOnNodeClick(node)" (click)="getOnClick(node)">
            <span *ngIf="node.icon">
              <amexio-nav-icon class="node-icon" [customclass]="node.icon"></amexio-nav-icon>
            </span>
          <span >{{node.text}}</span>
          <ng-container *ngIf="node.badge">
            <span  class="nav-badge" >{{node.badge}}</span>
          </ng-container>
          <ng-container *ngIf="node.children && (node.children.length>0)">
                <span style="padding-right: 10px; float: right" *ngIf="node.expand" >
                  <amexio-nav-icon key="itemselector_caretup"></amexio-nav-icon></span>

            <span  style="padding-right: 10px;float: right;" *ngIf="!node.expand" >
                  <amexio-nav-icon key="itemselector_caretdown"></amexio-nav-icon>
                </span>
          </ng-container>
        </div>
        <div class="submenu-details"  [ngClass]="{'sidenav-submenu-expanded':node.expand}" >
          <amexio-sidenav-node *ngIf="node.children && (node.children.length>0)" [enable-drag]="enabledrag"   [data]="node.children" (onDrag)="dragStartEvent($event)"  (nodeClick)="getOnNodeClick($event)" (onClick)="getOnClick($event)">
          </amexio-sidenav-node>
        </div>
      </li>
    </ul>
  `,
})
export class SideNavNodeComponent implements OnInit {

  /*
   Properties
   name : data
   datatype : any
   version : 4.0 onwards
   default : none
   description : Local data for sidenav.
   */
  @Input() data: any[];

  /*
   Properties
   name : enable-drag
   datatype : boolean
   version : 5.0.0 onwards
   default : false
   description : nodes can be dragged
   */
  @Input('enable-drag') enabledrag: boolean;

  /*
   Events
   name : onClick
   datatype : none
   version : none
   default : none
   description : fires on the click event
   */
  @Output() onClick: any = new EventEmitter<any>();

  /*
   Events
   name : nodeClick
   datatype : none
   version : none
   default : none
   description : Fire when sidenav bar menu click
   */
  @Output() nodeClick: any = new EventEmitter<any>();

  /*
   Events
   name : onDrag
   datatype : none
   version : 4.2.9
   default : none
   description : Fire when you drag node
   */
  @Output() onDrag: any = new EventEmitter<any>();

  constructor() {
  }

  ngOnInit() {
  }

  getOnClick(node: any) {
    this.onClick.emit(node);
  }

  activateNode(data: any[], node: any) {
    for (const i of data) {
      if (node === i && !i['children']) {
        i['active'] = true;
      } else {
        i['active'] = false;
      }

      if (i['children']) {
        this.activateNode(i['children'], node);
      }
    }
  }

  getOnNodeClick(node: any) {
    this.nodeClick.emit(node);
  }

  dragStartEvent(nodeData: any) {
    this.onDrag.emit(nodeData);
  }
}
